import "./App.scss";
import React, { useState, useEffect } from "react";
import {
  BrowserRouter as Router,
  Route,
  Routes as Switch,
  useLocation,
} from "react-router-dom";

import { AnimatePresence, motion } from "framer-motion";

import Home from "./components/Home";
import About from "./components/About";
import Services from "./components/Services";
import Admin from "./components/Admin";
import Service1 from "./components/Serv1";
import Service2 from "./components/Serv2";
import Service3 from "./components/Serv3";
import Service4 from "./components/Serv4";
import Service5 from "./components/Serv5";
import Service6 from "./components/Serv6";

import Navigation from "./components/Navigation";
import Footer from "./components/Footer";

import "bootstrap/dist/css/bootstrap.min.css";

import hero2_bg from "./images/hero2-bg.jpg";

function App() {
  useEffect(() => {
    //  scroll to top on page load
    window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
  }, []);
  const location = useLocation();

  const [width, setWidth] = useState(window.innerWidth);

  useEffect(() => {
    function handleResize() {
      setWidth(window.innerWidth);
    }
    window.addEventListener("resize", handleResize);
  });
  return (
    <div
      className="masthead"
      style={{
        backgroundImage: `url(${hero2_bg})`,
        minHeight: "100vh",
        textShadow: "none",
      }}
    >
      <Navigation _path={window.location.pathname} />
      <AnimatePresence mode="wait">
        <Switch location={location} key={location.key}>
          <Route path="/" element={<Home />} />
          <Route path="/about" element={<About />} />
          <Route path="/services" element={<Services />} />
          <Route path="/admin" element={<motionAdmin />} />
          <Route path="/service1" element={<Service1 />} />
          <Route path="/service2" element={<Service2 />} />
          <Route path="/service3" element={<Service3 />} />
          <Route path="/service4" element={<Service4 />} />
          <Route path="/service5" element={<Service5 />} />
          <Route path="/service6" element={<Service6 />} />
        </Switch>
      </AnimatePresence>
      <Footer _path={window.location.pathname} />
    </div>
  );
}

export default App;
