import React from "react";
import Button from "react-bootstrap/Button";
import {
  MDBFooter,
  MDBContainer,
  MDBCol,
  MDBRow,
  MDBIcon,
  MDBBtn,
} from "mdb-react-ui-kit";

const Footer = ({ _path }) => {
  return (
    <MDBFooter className="text-center text-fourth">
      <MDBContainer className="pt-4">
        <section className="mb-4 text-fourth">
          <Button
            color="link"
            size="lg"
            className=" m-1 text-fourth bg-transparent  border-0"
            href="#!"
            role="button"
          >
            <MDBIcon fab className="fab fa-facebook-f" />
          </Button>

          <Button
            color="link"
            size="lg"
            className=" m-1 text-fourth bg-transparent  border-0"
            href="#!"
            role="button"
          >
            <MDBIcon fab className="fa-twitter" />
          </Button>

          <Button
            color="link"
            size="lg"
            className=" m-1 text-fourth bg-transparent  border-0"
            href="#!"
            role="button"
          >
            <MDBIcon fab className="fa-vk" />
          </Button>

          <Button
            color="link"
            size="lg"
            className=" m-1 text-fourth bg-transparent  border-0"
            href="#!"
            role="button"
          >
            <MDBIcon fab className="fa-instagram" />
          </Button>
        </section>
      </MDBContainer>

      <div className="text-center p-3 bg-fourth text-first">
         2023,   :
        <a
          className="text-white ms-3 text-decoration-none "
          href="http://mad-media.ru"
        >
          MAD media
        </a>
      </div>
    </MDBFooter>
  );
};

export default Footer;
